.TechnicalAnalysis-Main-Container {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: scroll;
}

thead td {
  text-align: left;
  padding: 4px 10px;
  font-weight: 600;
  font-size: 14px;
  position: sticky;
  top: 0;
  background: rgb(241, 241, 243);
  white-space: nowrap;
}
tbody td {
  text-align: left;
  padding: 4px 10px;
  font-weight: 400;
  font-size: 14px;
  white-space: nowrap;
}


.scrip-addation-button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
}
tbody tr:hover {
  background: rgb(249, 249, 249);
}
#Edit-row,
#delete-row {
  cursor: pointer;
}
.addScrip {
  background:white ;
  padding: 5px 5px;
  cursor: pointer;
  outline: none;
  border: 0px;
  display: flex;
  align-items: center;
  color: rgb(66, 81, 110);
  font-size: 14px;
  font-family: inherit;
  border-radius: 3px;
  text-decoration: underline;
}

.tab {
  background: rgb(66, 81, 110);
  padding: 5px 15px;
  margin-left: 10px;
  cursor: pointer;
  outline: none;
  border: 0px;
  display: flex;
  align-items: center;
  color: white;
  font-size: 14px;
  font-family: inherit;
  border-radius: 3px;
}

.tab:hover{
  background-color: royalblue;
}
.active{
  background-color: royalblue;
  padding: 5px 15px;
  margin-left: 10px;
  cursor: pointer;
  outline: none;
  border: 0px;
  display: flex;
  align-items: center;
  color: white;
  font-size: 14px;
  font-family: inherit;
  border-radius: 3px;
}
.numric-values {
  text-align: right;
}
#alert-row,
#non-alert-row {
  cursor: pointer;
}
#message-id {
  font-size: 16px;
}
button:disabled {
  cursor: not-allowed;
  pointer-events: all !important;
}

.sucess{

}

.sucess td{
  padding: 20px 0px;
  background: #f1f1f3;
  font-weight: 600;
  font-size: 16px;
}

