.UserInfoBar-Main-Container {
  background-color: #42516e;
  text-transform: uppercase;
  color: white;
  padding: 15px;
  box-shadow: 1px 1px 1px gray;
  min-width: 100px;
}
.UserInfoBar-contents-container {
  display: flex;
  flex-direction: column;
  box-shadow: 0px 0px 7px #f1e7e7;
  border-radius: 5px;
  margin: 10px;
  cursor: pointer;
}
